@import url(https://fonts.googleapis.com/css2?family=Mulish:ital@1&display=swap);
*{box-sizing:border-box;margin:0px;padding:0%;font-family:'Mulish', sans-serif}.navbar{background-color:cornsilk !important}.navbar a{font-size:1.3rem;text-transform:capitalize}.navbar a:hover{color:#007b8b !important}.navbar .navbar-brand{background-color:cornsilk !important;font-size:1.9rem !important}.navbar .menu_active{font-weight:bold;border-bottom:indigo 1px solid}#header{width:100%;height:80vh;margin-top:70px}#header h1{margin:0 0 10px 0;font-size:48px;font-weight:700;color:black}#header .brand-name{color:#027c68}#header h2{color:#1d0101;font-size:24px}#header .btn-get-started{font-weight:500;font-size:16px;letter-spacing:1px;display:inline-block;padding:10px 30px;border-radius:50px;transition:0.5sec;color:#027c68;text-decoration:none;border:2px solid #027c68}#header .btn-get-started:hover{background:#027c68;color:#fff}.header-img{text-align:right;background:transparent}#header .img-fluid-animated{animation:up-down 2s ease-in-out infinite alternate-reverse both}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@media (max-width: 990px){.navbar-brand{background-color:cornsilk !important;font-size:1.5rem !important}.menu_active{font-weight:bolder;border-bottom:none}}

